.menuContainer {
  color: var(--main-header-color);
  margin: 10px 20px;
}

.menuContainer ul {
  list-style-type: none;
}

.menuContainer ul li {
  transition: var(--transition-medium);
  cursor: pointer;
  margin: 10px 0 0;
  padding: 10px 15px;
}

.menuContainer ul li a {
  color: inherit;
  transition: var(--transition-medium);
  cursor: pointer;
  margin: 10px;
  text-decoration: none;
  position: relative;
}

li > a:hover:before,
li > a:focus:before {
  visibility: visible;
  transform: scaleX(1);
}

li > a:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 3px;
  bottom: 0;
  left: 0;
  background: #a37fc6;
  visibility: hidden;
  border-radius: 5px;
  transform: scaleX(0);
  transition: 0.25s linear;
}
