:root {
  --main-white: #fff;
  --transition-slow: all 1s ease-in-out;
  --transition-medium: all 0.5s ease-in-out;
  --transition-fast: all 0.25s ease-in-out;
  --main-header-color: #8f77a7;
  --main-p-color: #955088;

  --about-bg-color: #6e508c;
  --work-bg-color: #a37fc6;
  --writings-bg-color: #cfbee0;
  --dark-purple: #523273;
  --dark-purple2: #5f1751;
}

.color-primary-2 {
  color: #8f77a7;
}

.color-secondary-1-0 {
  color: #955088;
} /* Main Secondary color (1) */

html,
body {
  margin: 0;
  font-family: 'Ubuntu', 'Muli', sans-serif, auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

