:root {
  --cube-size: 100px;
  --half-cube: 50px;
  --half-cube-neg: -50px;
}

/*************** ANIMATIONS ***************/
@-webkit-keyframes cube-spin {
  from {
    -webkit-transform: rotateY(0);
  }

  to {
    -webkit-transform: rotateY(360deg);
  }
}

@-ms-keyframes cube-spin {
  from {
    ms-transform: rotateY(0);
  }

  to {
    ms-transform: rotateY(360deg);
  }
}

@keyframes cube-spin {
  from {
    transform: rotateY(0);
  }

  to {
    transform: rotateY(360deg);
  }
}

@-webkit-keyframes spin-vertical {
  from {
    -webkit-transform: rotateX(0);
  }

  to {
    -webkit-transform: rotateX(-360deg);
  }
}

@-ms-keyframes spin-vertical {
  from {
    ms-transform: rotateX(0);
  }

  to {
    ms-transform: rotateX(-360deg);
  }
}

@keyframes spin-vertical {
  from {
    transform: rotateX(0);
  }

  to {
    transform: rotateX(-360deg);
  }
}

/*************** STANDARD CUBE ***************/
.cube-wrap {
  -webkit-perspective: 800px;
  -webkit-perspective-origin: 50% 100px;

  -moz-perspective: 800px;
  -moz-perspective-origin: 50% 100px;

  -ms-perspective: 800px;
  -ms-perspective-origin: 50% 100px;

  perspective: 800px;
  perspective-origin: 50% 100px;
}

.cube {
  text-transform: capitalize;
  position: relative;
  width: var(--cube-size);
  margin: 0 auto;

  -webkit-transform-style: preserve-3d;
  -webkit-animation: cube-spin infinite 20s linear;

  -moz-transform-style: preserve-3d;
  -moz-animation: cube-spin infinite 20s linear;

  -ms-transform-style: preserve-3d;
  -ms-animation: cube-spin infinite 20s linear;

  transform-style: preserve-3d;
  animation: cube-spin infinite 20s linear;
}

.cube div {
  position: absolute;
  width: var(--cube-size);
  height: var(--cube-size);
  background: rgba(255, 255, 255, 0.1);
  box-shadow: inset 0 0 30px rgba(125, 125, 125, 0.8);
  font-size: 20px;
  text-align: center;
  line-height: var(--cube-size);
  color: rgba(0, 0, 0, 0.5);
}

/*************** DEPTH CUBE ***************/
.depth div.back-pane {
  -webkit-transform: translateZ(var(--half-cube-neg)) rotateY(180deg);
  -moz-transform: translateZ(var(--half-cube-neg)) rotateY(180deg);
  -ms-transform: translateZ(var(--half-cube-neg)) rotateY(180deg);

  transform: translateZ(var(--half-cube-neg)) rotateY(180deg);
}

.depth div.right-pane {
  -webkit-transform: rotateY(-270deg) translateX(var(--half-cube));
  -webkit-transform-origin: top right;

  -moz-transform: rotateY(-270deg) translateX(var(--half-cube));
  -moz-transform-origin: top right;

  -ms-transform: rotateY(-270deg) translateX(var(--half-cube));
  -ms-transform-origin: top right;

  transform: rotateY(-270deg) translateX(var(--half-cube));
  transform-origin: top right;
}

.depth div.left-pane {
  -webkit-transform: rotateY(270deg) translateX(-50px);
  -webkit-transform-origin: center left;

  -moz-transform: rotateY(270deg) translateX(-50px);
  -moz-transform-origin: center left;

  -ms-transform: rotateY(270deg) translateX(-50px);
  -ms-transform-origin: center left;

  transform: rotateY(270deg) translateX(-50px);
  transform-origin: center left;
}

.depth div.top-pane {
  -webkit-transform: rotateX(-90deg) translateY(-50px);
  -webkit-transform-origin: top center;

  -moz-transform: rotateX(-90deg) translateY(-50px);
  -moz-transform-origin: top center;

  -ms-transform: rotateX(-90deg) translateY(-50px);
  -ms-transform-origin: top center;

  transform: rotateX(-90deg) translateY(-50px);
  transform-origin: top center;
}

.depth div.bottom-pane {
  -webkit-transform: rotateX(90deg) translateY(50px);
  -webkit-transform-origin: bottom center;

  -moz-transform: rotateX(90deg) translateY(var(--half-cube));
  -moz-transform-origin: bottom center;

  -ms-transform: rotateX(90deg) translateY(var(--half-cube));
  -ms-transform-origin: bottom center;

  transform: rotateX(90deg) translateY(var(--half-cube));
  transform-origin: bottom center;
}

.depth div.front-pane {
  -webkit-transform: translateZ(var(--half-cube));
  -moz-transform: translateZ(var(--half-cube));
  -ms-transform: translateZ(var(--half-cube));

  transform: translateZ(var(--half-cube));
}
