.about {
  position: relative;
  width: 60%;
  margin: -15px auto;
  color: var(--main-header-color);
  mix-blend-mode: difference;
  outline: none;
  padding: 2px;
}

.about h1 {
  position: relative;
  color: var(--main-header-color);
}

.about p {
  font-size: 20px;
  color: var(--main-p-color);
  text-shadow: 1px 1px var(--main-header-color);
}
