.writings {
  position: relative;
  width: 60%;
  margin: -15px auto;
  color: var(--main-header-color);
  mix-blend-mode: difference;
  outline: none;
  padding: 2px;
}

.companyAndBrands {
}

.workDescription {
}

.workDescription > div {
  order: 0;
  flex: 0 1 auto;
  align-self: auto;
}

.writings h1 {
  position: relative;
  color: var(--main-header-color);
}

.writings p {
  font-size: 20px;
  color: var(--main-p-color);
  text-shadow: 1px 1px var(--main-header-color);
}

.topics {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: flex-start;
  align-items: flex-start;
}

.topicItem {
  margin: 8px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}

.topicImage {
  width: 200px;
  min-height: 200px;
  object-fit: scale-down;
}

.topicItem:hover {
  transform: scale(1.1);
  border-radius: 5px;
  background: var(--main-white);
  display: block;
}

.itemName {
  display: none;
}
