.workContainer {
}

.companyAndBrands {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: flex-start;
  align-items: flex-start;
}

.workDescription {
  transition: var(--transition-medium);
  position: absolute;
  margin: -200px -2px;
  padding: 40px 30px;
  width: 140px;
  height: 80px;
  border: 2px solid transparent;
  color: white;
  background: var(--dark-purple);
  overflow: scroll;
}

.workDescription > div {
  order: 0;
  flex: 0 1 auto;
  align-self: auto;
}

.companyName,
.role {
  color: white;
  text-decoration: none;
  text-transform: capitalize;
}

.role {
  text-align: center;
  font-size: 12px;
}

.opacityEffect {
  opacity: 0;
}

.opacityEffect:hover {
  opacity: 1;
}

.workImage {
  image-rendering: auto;
  min-height: 30px;
  object-fit: scale-down;
  min-width: 50px;
  max-width: 160px;
  margin: 3px;
}

.work-item {
  margin: 10px;
  padding: 2px;
  width: 180px;
  opacity: 1;
  transition: all 0.5s ease-in-out;
}

.work-item:hover {
  border: 1px solid var(--dark-purple);
  transform: scale(1.1);
  overflow: hidden;
}

/*End Flexbox layout 4 desktop*/
/* tablet mobile */
@media (min-width: 641px) {
  .companyAndBrands {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-content: space-around;
    align-items: center;
    height: auto;
    margin: 20px;
    max-width: 90vw;
  }
}
