.footerContainer {
  background-color: #282c34;
  height: 250px;

  /* TODO change tp better values when u know why the vh is weird*/
  display: flex;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: var(--main-header-color);
}

.footerSection {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-end;
  padding-bottom: 250px;
  width: 200vw;
}

.footerSection .footerWave {
  background: url('./wave.svg') repeat;
  background-size: cover;
  transform: rotate(180deg);
  padding-bottom: 250px;
  width: 200vw;
  margin-left: -100vw;
}
